@import url(https://fonts.googleapis.com/css?family=Stoke&display=swap);
:root {
  --app-width: 480px;
  --card-width: 130px;
}

@media screen and (max-width: 480px) {
  :root {
    --app-width: 400px;
    --card-width: 120px;
  }
}

body,
p {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}

.app-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: var(--app-width);
}

.types-container {
  margin-top: 5%;
  flex: 1 1;
  display: flex;
  justify-content: space-around;
}

.list-container {
  margin-top: 5%;
  flex: 1 1;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.info-container {
  border-radius: 5px;
  background-color: #00d31c;
  margin: 5% 0;
  padding-bottom: 3%;
  flex: 1 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.info-container p {
  font-family: 'Stoke', serif;
  text-align: center;
}

.card-container {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border-radius: 5px;
  box-shadow: 2px 2px 4px grey;
  width: var(--card-width);
  height: var(--card-width);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin: 10px 0
}

.card-container img {
  height: 65%;
}

.card-container p {
  font-family: 'Stoke', serif;
  text-align: center;
}

